function getUserIndex (users, user) {
  for (let i = 0; i < users.length; i++) {
    if (users[i].id === user.id) {
      return i
    }
  }
  return -1
}

function getUserIndexById (users, id) {
  for (let i = 0; i < users.length; i++) {
    if (users[i].id === id) {
      return i
    }
  }
  return -1
}

