  static addUserInTournament (tournament, user) {
    let index = getTournamentIndex(this.allTournaments, tournament)
    if (index === -1) {
      return
    }

    this.allTournaments[index].participants.push(user.id)
  }

  static deleteUserFromTournament (tournament, user) {
    let index = getTournamentIndex(this.allTournaments, tournament)
    if (index === -1) {
      return
    }

    this.allTournaments[index].participants =
      this.allTournaments[index].participants.filter((id) => id !== user.id)
  }

  static getTournamentParticipants (tournament) {
    let index = getTournamentIndex(this.allTournaments, tournament)
    if (index === -1) {
      return
    }

    return this.allTournaments[index].participants
  }

